.slider {
    width: 100%;
    position: relative;
    margin-bottom: 40vh;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-animation: slider 6s infinite;
            animation: slider 6s infinite;
  }
@media(min-width: 600px){
    .slider{
        margin-bottom: 54vh;
    }
}
@keyframes slider {
    0%, 30%{ -webkit-transform: rotateY(-2.5deg); transform: rotateY(-2.5deg); }
    33.33333%, 63.33333%{ -webkit-transform: rotateY(-119deg); transform: rotateY(-119deg); }
    66.66666%, 96.66666%{ -webkit-transform: rotateY(-239deg); transform: rotateY(-239deg); }
    100%{ -webkit-transform: rotateY(-360deg); transform: rotateY(-360deg); }
}
@-webkit-keyframes slider {
    0%, 30%{ -webkit-transform: rotateY(-2.5deg); transform: rotateY(-2.5deg); }
    33.33333%, 63.33333%{ -webkit-transform: rotateY(-119deg); transform: rotateY(-119deg); }
    66.66666%, 96.66666%{ -webkit-transform: rotateY(-239deg); transform: rotateY(-239deg); }
    100%{ -webkit-transform: rotateY(-360deg); transform: rotateY(-360deg); }
}
.demo a {
  position: absolute;
  top: 60px;
  left: 50%;
  z-index: 205;
  display: inline-block;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
  color: #fff;
  font : normal 400 20px/1 'Josefin Sans', sans-serif;
  letter-spacing: .1em;
  text-decoration: none;
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.demo a:hover {
  opacity: .5;
}
#scroll_down a {
  padding-top: 80px;
}
#scroll_down a span {
  position: absolute;
  top: 0;
  left: 50%;
  width: 24px;
  height: 24px;
  margin-left: -12px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-animation: sdb07 2s infinite;
  animation: sdb07 2s infinite;
  opacity: 0;
  box-sizing: border-box;
}
#scroll_down a span:nth-of-type(1) {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}
#scroll_down a span:nth-of-type(2) {
  top: 16px;
  -webkit-animation-delay: .15s;
  animation-delay: .15s;
}
#scroll_down a span:nth-of-type(3) {
  top: 32px;
  -webkit-animation-delay: .3s;
  animation-delay: .3s;
}
@-webkit-keyframes sdb07 {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes sdb07 {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

